import FishFloatStats from "./FishFloatStats";

const FishInfoFloat = (props) => {
    return (
        <div className='info-float-container'>
            <div className='info-float'>
                <div className='info-float-img flex'>
                    { Array.isArray(props.gallery) && <img src={props.gallery[0].src} alt={props.gallery[0].alt}/>}
                    { !Array.isArray(props.gallery) && props.gallery !== null && <img src={props.gallery.src} alt={props.gallery.alt}/>}
                </div>
                <div className='info-float-stats-container'>
                    <FishFloatStats stat='Population' data={ props.data.Population } newClass={ 'info-float-stats' }/>
                    <FishFloatStats stat='Fishing Rate' data={ props.data['Fishing Rate'] } newClass={ 'info-float-stats' }/>
                    <FishFloatStats stat='Availability' data={ props.data.Availability } newClass={ 'info-float-stats' }/>
                    <FishFloatStats stat='Color' data={ props.data.Color } newClass={ 'info-float-stats-bottom' }/>
                </div>
                <div className='info-float-taste-container'>
                    <FishFloatStats stat='Taste' data={ props.data.Taste } newClass={ 'info-float-stats' }/>
                    <FishFloatStats stat='Texture' data={ props.data.Texture } newClass={ 'info-float-stats' }/>
                    <FishFloatStats stat='Health Benefits' data={ props.data['Health Benefits'] } newClass={ 'info-float-stats-bottom' }/>
                </div>
            </div>
        </div>
    );
}
 
export default FishInfoFloat;