import DOMPurify from "dompurify";

const FishFloatStats = (props) => {
    return (
        <div className={props.newClass}>
            <div className='info-float-stat-title'>{props.stat}</div>
            <div className='info-float-stat-body' dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(props.data)}}></div>
        </div>
    );
}
 
export default FishFloatStats;