import DOMPurify from "dompurify";

const FishInfoBox = (props) => {
    return (
        <div className={props.boxClass}>
                <h3>{ props.title }</h3>
                <div dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(props.body)}}></div>
        </div>
    );
}
 
export default FishInfoBox;