import { useState } from "react";
import RandomCatchItem from "./RandomCatchItem";
import useFetch from "./useFetch";

const RandomCatch = () => {

    const { data, isPending, error } = useFetch('https://raw.githubusercontent.com/iMeece/fishpedia-data/main/db.json');
    const [, setRandCatch] = useState([]);

    function checkCatch(randomCatch, newCatch, catchLimit) {

        while (randomCatch.includes(newCatch) || data[newCatch]['Image Gallery'] === null) {

            newCatch += 1;

            if (newCatch === catchLimit) {
                newCatch = 0;
            };
        };

        return newCatch;
    }

    function generateRandomCatchId(data) {
        const catchLimit = 115;
        let randomCatch = [];

        for (let i = 0; i < 8; i++) {
            let newCatch = Math.floor(Math.random() * catchLimit);
            newCatch = checkCatch(randomCatch, newCatch, catchLimit);
            randomCatch.push(newCatch);
        };

        return randomCatch;
    }

    function randomizeCatch() {
        setRandCatch(generateRandomCatchId(data));
    }

    return (
        <div className='random-catch-container flex'>
            <h4>Random Catch</h4>
            <div className='random-catch flex'>
                { error && <div>An error has occurred.</div> }
                { isPending && <h2>Catching Fish...</h2> }
                { data && <RandomCatchItem fishData={ data } randomCatch={ generateRandomCatchId(data) } /> }
            </div>
            <button className='random-catch-button' onClick={randomizeCatch}>Randomize</button>
        </div>
    );
}
 
export default RandomCatch;