import Nav from "./Nav";
import logo from "../Images/navlogo.png"

const Navbar = () => {
    return (
        <div className='navbar-container flex'>
            <div className='navbar-background'>
                <div className='navbar flex'>
                    <div className='navlogo flex'>
                        <img src={logo} alt='fishlogo' />
                        <h4>Fishpedia</h4>
                    </div>
                    <Nav />
                </div>
            </div>
        </div>
    );
}
 
export default Navbar;