import { Link } from "react-router-dom";

const FishInfoImages = (props) => {
    return (
        <div className='info-images info-item'>
            <h3>Image Gallery</h3>
            <div className='info-images-list flex'>
                { Array.isArray(props.gallery) && 
                    props.gallery.map((image, index) => (
                        <Link to='' key={ props.fishId + '-' + index }><img src={props.gallery[index].src} alt={props.gallery[index].alt}/></Link>
                    ))
                }
                { !Array.isArray(props.gallery) && props.gallery !== null && <img src={props.gallery.src} alt={props.gallery.alt}/>}
            </div>
        </div>
    );
}

export default FishInfoImages;