import { useState } from "react";
import NavLink from "./NavLink";

const Nav = () => {

    const [searchActive, setSearchActive] = useState(false);

    function switchSearchActive() {
        setSearchActive(!searchActive);
    }

    function handleSearchEvent(e) {
        if (e.keyCode === 13) {
            switchSearchActive();
            // Should actually move to a different link
        };
    }

    return (
        <div className='nav flex'>
            <NavLink link={'/'} name={'Home'}/>
            <NavLink link={'/random'} name={'Random'}/>
            {!searchActive && <div className='link-searchglass' onClick={switchSearchActive}><i className='fa fa-search'></i></div>}
            {searchActive && <input className='nav-searchbar' type='text' onKeyUp={handleSearchEvent} />}
        </div>
    );
}
 
export default Nav;