import ShowcaseItem from "./ShowcaseItem";

const Showcase = () => {

    const detailBox = [
        {
            title: 'About',
            body: 'Wikipedia containing data about a variety of fish from all regions.'
        },

        {
            title: 'Database',
            body: 'Information and data collected from',
            body2: 'database.',
            link: <a href='https://www.fishwatch.gov/'>fishwatch</a>
        },

        {
            title: 'Contact',
            body: 'Wikipedia containing data about a variety of fish from all regions.'
        }
    ];

    return (
        <div className='showcase-container'>
            <div className='showcase flex'>
                <ShowcaseItem title={detailBox[0].title} details={detailBox[0].body}/>
                <ShowcaseItem title={detailBox[1].title} details={detailBox[1].body} link={detailBox[1].link} details2={detailBox[1].body2}/>
                <ShowcaseItem title={detailBox[2].title} details={'details'}/>
            </div>
        </div>
    );
}
 
export default Showcase;