import FishInfoBox from "./FishInfoBox";
import FishInfoFloat from "./FishInfoFloat";
import FishInfoImages from "./FishInfoImages";

const FishInfo = (props) => {

    const fishDescription = props.fish['Physical Description'];
    const fishBiology = props.fish.Biology;
    const fishHabitat = props.fish.Habitat;
    const fishLocation = props.fish.Location;
    const fishResearch = props.fish.Research;

    return (
        <div className='fish-info-container'>
            <FishInfoFloat gallery={ props.fish['Image Gallery']} data={ props.fish }/>
            <div className='info-name'>
                <h2>{ props.fish['Species Name'] }</h2>
                <h6>{ props.fish['Scientific Name'] }</h6>
            </div>
            <FishInfoBox boxClass={'info-description info-item'} title={'Description'} body={fishDescription}/>
            <FishInfoBox boxClass={'info-biology info-item'} title={'Biology'} body={fishBiology}/>
            <FishInfoBox boxClass={'info-habitat info-item'} title={'Habitat'} body={fishHabitat}/>
            <FishInfoBox boxClass={'info-location info-item'} title={'Location'} body={fishLocation}/>
            <FishInfoBox boxClass={'info-research info-item'} title={'Research'} body={fishResearch}/>
            <FishInfoImages gallery={ props.fish['Image Gallery'] } fishId={ props.fishId }/>
        </div>
    );
}
 
export default FishInfo;